
import RecoveryForm from 'components/Forms/RecoveryForm'
import HomePage from 'features/HomePage'
import KabinetPage from 'features/KabinetPage'
import LoginPage from 'features/LoginPage'
import React from 'react'
import { Route, Switch } from 'react-router-dom'

export const Routes = () => {
    return (
        <Switch>
            <Route exact path={`${process.env.PUBLIC_URL}/`} component={HomePage} />
            <Route exact path={`${process.env.PUBLIC_URL}/login`} component={LoginPage} />
            <Route path={`${process.env.PUBLIC_URL}/login/recoveryPassword`} component={RecoveryForm} />
            <Route path={`${process.env.PUBLIC_URL}/cabinet`} component={KabinetPage} />
        </Switch>

    )
}
